// *** BEGIN CORE CSS FILE ==================================================== ***
// NOTE: This file should contain nothing but import statements.                ***
// GENERAL USE NOTES                                                            ***
// ============================================================================ ***
// These general rules are to keep the overall CSS and SCSS clean. They are     ***
// ony included in the top level style sheet for thie site.                     ***
// ---------------------------------------------------------------------------- ***
// The purpose of each file:                                                    ***
// A file should be either an aggregator or a collection of rules, not both.    ***
// If a partial includes rules from another partial,                            ***
// make sure the source appears first in the parent aggregator.                 ***
//                                                                              ***
// Order of operations:                                                         ***
// - extend (shouldn't use, but it comes first if you do)                       ***
// - include                                                                    ***
// - simple selectors                                                           ***
// - & selectors                                                                ***
// - nested selectors                                                           ***
//                                                                              ***
// Comment usage:                                                               ***
// - /*!  - Always included in compile.                                         ***
// - /**  - Include in any system generated docs and verbose CSS compiles.      ***
//          We don't generate CSS docs, this is just here from completeness.    ***
// - /*   - Included in verbose CSS compiles.                                   ***
// - //   - Exclude from everything but source.                                 ***
// For example, this block uses the double slash single line comment because    ***
// there is no reason for it to appear anywhere but in this file.               ***
// ============================================================================ ***

